.custom-searchbar {
  z-index: 1001;
  position: absolute;
  // Fallback pour Android - utilise une valeur fixe minimum
  top: max(var(--ion-safe-area-top), 24px);
  left: 2.5%;
  width: 95%;
  margin-top: 16px;
  max-width: 400px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 54px;
  height: 60px;
  padding: 15px;
  display: flex;
  align-items: center;
  color: var(--ion-color-dark);

  &.focused {
    background-color: #f2f2f2;
    box-shadow: none;
    border: none;
  }
  // Solution spécifique pour Android
  @supports not (top: max(var(--ion-safe-area-top), 24px)) {
    // Fallback pour navigateurs qui ne supportent pas max()
    top: var(--ion-safe-area-top);
    padding-top: 24px;
  }
}

// Alternative robuste pour Android uniquement
.custom-searchbar.android-safe-area {
  top: calc(var(--ion-safe-area-top) + 24px);
  min-height: 60px;

  // Assure une position minimale même si safe area = 0
  @supports (top: max(40px, calc(var(--ion-safe-area-top) + 24px))) {
    top: max(40px, calc(var(--ion-safe-area-top) + 24px));
  }
}

// Solution pour différentes densités d'écran Android
@media screen and (-webkit-device-pixel-ratio: 2) {
  .custom-searchbar {
    top: calc(max(var(--ion-safe-area-top), 28px));
  }
}

@media screen and (-webkit-device-pixel-ratio: 3) {
  .custom-searchbar {
    top: calc(max(var(--ion-safe-area-top), 32px));
  }
}

// Support pour les anciens navigateurs Android
@supports not (top: max(var(--ion-safe-area-top), 24px)) {
  .custom-searchbar {
    top: env(safe-area-inset-top, 40px);
    margin-top: 24px;
  }
}

.searchbar-overlay {
  position: fixed;
  inset: 0; // Shorthand for top/right/bottom/left: 0
  background-color: white;
  z-index: 1000;
}

ion-input {
  --padding-start: 10px;
}

// Search results list
ion-list {
  // Add space below the searchbar
  width: 100%;
  --ion-background-color: white;
}

.searchbar-overlay {
  // Utilise la même logique pour l'overlay
  padding-top: calc(max(var(--ion-safe-area-top), 24px) + 76px);

  // Fallback pour navigateurs non compatibles
  @supports not (
    padding-top: calc(max(var(--ion-safe-area-top), 24px) + 76px)
  ) {
    padding-top: calc(var(--ion-safe-area-top) + 100px);
    min-height: 100vh;
  }
}
